.streams-gallery--heading {
  background-color: #956dd7;
  border-top: 0.0001px;
  margin: 0 auto;
  padding: 4vh 0;
  font-size: 26.25px; }

@media (min-width: 480px) {
  .streams-gallery--heading {
    font-size: calc(26.25px + (53 * (100vw - 480px) / 1480)); } }

.streams-gallery--navbar {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  height: calc(34.3px + (8.6 * (100vh - 300px) / 468));
  width: 100%;
  background-color: #6441a5;
  margin: 4vh auto 0; }

.navbar--btn {
  background-color: #6441a5;
  color: #fff;
  height: 100%;
  width: 30%;
  border: none;
  text-decoration: none;
  line-height: calc(34.3px + (8.6 * (100vh - 300px) / 468));
  font-size: 25px;
  cursor: pointer;
  padding: 0 10px;
  transition: all 300ms ease-in-out;
  border-radius: 25px; }

.navbar--btn:hover {
  background-color: #956dd7;
  color: #fff; }

.navbar--btn:focus {
  color: black;
  background-color: #fff;
  border-radius: 25px; }

.navbar--btn__active, .navbar--btn__active:hover {
  color: black;
  background-color: #fff;
  width: 40%; }

.streams-gallery {
  width: 95%;
  min-width: 288px;
  margin: 0 auto 5%;
  color: #fff;
  border: none;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background-color: #6441a5; }

.streams--stream, .streams--stream__all, .streams--stream__online, .streams--stream__offline {
  display: grid;
  padding: 10px;
  grid-gap: 10px; }

.streams--stream__all {
  grid-auto-flow: row dense;
  grid-template-columns: minmax(240px, 1fr); }

.streams--stream__online {
  grid-template-columns: minmax(240px, 1fr); }

.streams--stream__offline {
  grid-template-columns: minmax(150px, 1fr); }

@media (min-width: 360px) {
  .streams--stream__offline {
    grid-template-columns: repeat(2, minmax(150px, 1fr)); }
  .streams--stream__all {
    grid-template-columns: repeat(2, minmax(150px, 1fr)); } }

@media (min-width: 680px) {
  .streams--stream__online {
    grid-template-columns: repeat(2, minmax(300px, 1fr)); }
  .streams--stream__offline, .streams--stream__all {
    grid-template-columns: repeat(3, minmax(150px, 1fr)); } }

@media (min-width: 1000px) {
  .streams--stream__online {
    grid-template-columns: repeat(3, minmax(300px, 1fr)); }
  .streams--stream__offline, .streams--stream__all {
    grid-template-columns: repeat(4, minmax(150px, 1fr)); } }

@media (min-width: 1450px) {
  .streams--stream__online {
    grid-template-columns: repeat(4, minmax(300px, 1fr)); }
  .streams--stream__offline, .streams--stream__all {
    grid-template-columns: repeat(5, minmax(150px, 1fr)); } }

@media (min-width: 1600px) {
  .streams--stream__online {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); }
  .streams--stream__offline, .streams--stream__all {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); } }



